<template>
 <div class="hello">
    <div>
      <button id="firstBtn" @click="testClick()" ref="aa">{{testMsg}}</button>
    </div>
    <!-- nextTick()，是将回调函数延迟在下一次dom更新数据后调用，简单的理解是：当数据更新了，在dom中渲染后，自动执行该函数，
      ue 实现响应式并不是数据发生变化之后 DOM 立即变化，而是按一定的策略进行 DOM 的更新。$nextTick 是在下次 DOM 更新循环结束之后执行延迟回调，在修改数据之后使用 $nextTick，则可以在回调中获取更新后的 DOM，
 -->
  </div>
</template>
<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      testMsg: "原始值",
    }
  },
  methods: {
    testClick: function () {
      let that = this;
      that.testMsg = "修改后的值";
      // console.log(that.$refs.aa.innerText);   //that.$refs.aa获取指定DOM，输出：原始值
      that.$nextTick(function () {
        console.log(that.$refs.aa.innerText);   // 修改后的值
      })
    }
  }

}
</script>
<style lang='scss'>
</style>